<template>
<div class="v-container">
  <div class="upper-container">
    <b-navbar class="v-navbar" type="dark" variant="primary" toggleable>
        <b-navbar variant="faded" type="light">
          <b-navbar-brand href="/">
            <img class="d-inline-block align-top logo" src="@/assets/images/macluque-logo.jpg" />
          </b-navbar-brand>
        </b-navbar>
        <div class="v-title">
        {{Title}}
        </div>
      <b-navbar-nav v-on:click="checkDisplay" class="ml-auto">
        <div>
          <b-navbar-toggle target="nav_dropdown_collapse"></b-navbar-toggle>
          <b-collapse is-nav id="nav_dropdown_collapse">
            <b-navbar-nav v-bind:class="{'v-wrap': isDisplayed}">
              <b-nav-item to="/">Main</b-nav-item>
              <b-nav-item to="/about">About</b-nav-item>
              <b-nav-item to="/about">Contacto</b-nav-item>
              <b-nav-item to="/about">Horarios</b-nav-item>
              <b-nav-item to="/about">Localización</b-nav-item>
              <b-nav-item to="/about">Precios</b-nav-item>
              <b-nav-item-dropdown text="Condiciones generales" right>
                <b-dropdown-item href="#">Condiciones generales</b-dropdown-item>
                <b-dropdown-item href="#">Condiciones generales privadas</b-dropdown-item>
              </b-nav-item-dropdown>
            </b-navbar-nav>
          </b-collapse>
          </div>
      </b-navbar-nav>
  </b-navbar>
  </div>
</div>
</template>

<script>


  export default {
    name: 'Header',
    data () {
      return {
        Title: 'LEARNING NOT STUDYING',
        isDisplayed: false
      }
    }, methods: {
      checkDisplay: function() {
        var but = this.$children[0];
        if(but.$el.getAttribute('aria-expanded') === 'false')  {
          this.$data.isDisplayed = true;
        } else if(document.querySelector('#nav_dropdown_collapse').display === 'none') {
          this.$data.isDisplayed = false;
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
a {
  color: #0000ff;
}

img.logo {
  width:50px;
  height:50px;
}

.v-container {
  background-color: white;
}

.v-title {
  font-weight: bold;
  color:white;
  margin: 0 auto 0 auto;
  font-style: italic;
}

.v-navbar {
  height:100%
}

.v-wrap {
  flex-wrap:wrap;
  flex-direction:row;
}

.nav-item {
  width:33%;
  display: inline-flex;
}

.nav-item > a {
  width:100%;
  text-align: center;
}

.upper-container {
  width:100%;
}

</style>



