<template>
  <div>
    <Header />
    <div class="about">
      <div class="div-name">
        <p class="name">{{name}}</p>
      </div>
      <AboutMe />
    </div>
  </div>
</template>

<script>
import Header from "./Header";
import AboutMe from './AboutMe'

export default {
  name: "About",
  components: {
    Header,
    AboutMe
  },
  data() {
    return {
      name: "Carmen Sanchez Luque",
      address: "MACLUQUE School of English. Tesoro, 14 - Malasaña"
    };
  }
};

</script>

<style>
@font-face {
  font-family: "Signature of the Ancient";
  src: url("../assets/fonts/Signature_of_the_Ancient.woff");
  font-weight: bold;
}

.name {
  font-family: "Signature of the Ancient";
  font-size: 3rem;
  text-decoration: underline;
  -webkit-text-underline-position: under;
  -ms-text-underline-position: below;
  text-underline-position: under;
}

.about {
  margin-top:3rem;
  width:100%;
  height:47.5rem;
}

/*For mobile phones*/
@media only screen and (max-width: 768px) {
  .about {
    height:36.6rem;
    margin-top:3rem;
    width:100%;
  }
}
</style>



